/**
 * Site header
 */
.site-header {
  position: relative;
  width: 100%;
  min-height: $spacing-unit * 1.865;
  line-height: $base-line-height * $base-font-size * 2.25;
  background-color: $header-background-color;
  border-bottom: 1px solid $border-color-01;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.site-title {
  @include relative-font-size(1.625);
  font-weight: 300;
  letter-spacing: -1px;
  margin-bottom: 0;
  float: left;
  font-size: 3em;
  color: $site-title-color;

  @include media-query($on-palm) {
    padding-right: 45px;
  }

  &,
  &:visited {
    color: $site-title-color;
  }
}

.site-header-about-text {
  padding-top: $spacing-unit * 0.2;
  margin: 0;
  line-height: $base-line-height;
}

.site-nav {
  float: right;
  border: none; 
  background-color: inherit;

  label[for="nav-trigger"], #nav-trigger {
    display: none;
  }

  .nav-item {
    color: $text-color;
    &:not(:last-child) {
      margin-right: $spacing-unit * 0.5;
    }
  }

  @include media-query($on-medium) {
    position: absolute;
    top: 9px;
    right: $spacing-unit * 0.6;
    background-color: $background-color;
    border: 1px solid $border-color-01;
    border-radius: 5px;
    text-align: right;

    label[for="nav-trigger"] {
      display: block;
      float: right;
      width: 36px;
      height: 36px;
      cursor: pointer;
    }

    .menu-icon {
      float: right;
      width: 36px;
      height: 26px;
      line-height: 0;
      padding-top: 18px;
      text-align: center;

      &::before {
        font-family: "Font Awesome 7 Free";
        font-weight: 900;
      }

      > svg path {
        fill: $border-color-03;
      }
    }

    #nav-trigger {
      ~ label[for=nav-trigger] .menu-icon {
        &::before {
          content: "\f0c9";
          font-size: 1.25em;
        }
      }
      ~ .nav-items {
        clear: both;
        display: none
      }
      &:checked {
      ~ label[for=nav-trigger] .menu-icon {
        &::before {
          content: "\f00d";
          font-size: 1.5em;
        }
      }
        ~ .nav-items {
          display: block;
          padding-bottom: 5px;
          .nav-item {
            margin-left: 20px;
            padding: 5px 10px;
          }
        }
      }
    }

    .nav-item {
      display: block;
      margin-right: $spacing-unit * 0.5;
      padding: 5px 0;
      color: $text-color;
      line-height: $base-line-height;
    }
  }
}



/**
 * Site footer
 */

.site-footer {
  border-top: 1px solid $border-color-01;
  background-color: $footer-background-color;
  padding: $spacing-unit 0;
}

.footer-heading {
  @include relative-font-size(1.125);
  margin-bottom: $spacing-unit * .5;
}

.feed-subscribe .svg-icon {
  padding: 5px 5px 2px 0;
}

.contact-list,
.social-media-list {
  list-style: none;
  margin-left: 0;
}

.social-links {
  @include relative-font-size(0.9375);
  color: $brand-color-dimmed;
}

/**
 * Page content
 */
.page-content {
  padding: $spacing-unit 0;
  flex: 1 0 auto;
}

.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  @include relative-font-size(1.75);
}

.post-list {
  margin-left: 0;
  list-style: none;

  > li {
    margin-bottom: $spacing-unit * 1.5;
  }
  
  h3 {
    border: 0;
    padding: 0;
    margin: 0;
  }
}

.post-meta {
  font-size: $small-font-size;
  color: $brand-color-dimmed;
  .post-tags {
    color: $brand-color-dimmed;
  }
}

.post-link {
  display: block;
  @include relative-font-size(1.5);
  margin: 0;
}

.post-meta-after {
  display: block;
  margin-top: 0;
  padding-top: 0;
  padding-bottom: $spacing-unit * 0.1;
}

/**
 * Posts
 */
.post-header {
  margin-bottom: $spacing-unit;
  padding-bottom: 18px;
  text-align: center;
  border-bottom: 1px solid $border-color-01;
}

.post-meta {
  .bullet-divider { padding-inline: 15px }
  .meta-label { font-weight: 600 }

  .force-inline {
    display: inline;
    &::before {
      content: "â€¢";
      padding-inline: 5px;
    }
  }
  .post-authors { margin-top: 3px }
}

.post-title,
.post-content h1 {
  margin-bottom: 10px;
  @include relative-font-size(2.625);
  font-weight: $base-font-weight;
  letter-spacing: -1px;
  line-height: 1.15;

  @media screen and (min-width: $on-large) {
    @include relative-font-size(2.625);
  }
}

.post-content {
  margin-bottom: $spacing-unit;
  counter-reset: figure-counter;

  h1, h2, h3, h4, h5, h6 { margin-top: $spacing-unit }

  h2 {
    @include relative-font-size(1.75);

    @media screen and (min-width: $on-large) {
      @include relative-font-size(2);
    }
  }

  h3 {
    @include relative-font-size(1.375);

    @media screen and (min-width: $on-large) {
      @include relative-font-size(1.625);
    }
  }

  h4 {
    @include relative-font-size(1.25);
  }

  h5 {
    @include relative-font-size(1.125);
  }
  h6 {
    @include relative-font-size(1.0625);
  }
}

.related-posts {
  border-top: 1px solid $border-color-01;
  padding-top: $spacing-unit;
}

.comments-disabled-message {
  text-align: center;
  font-weight: 300;
}

.social-media-list {
  display: table;
  margin: 0 auto;
  li {
    float: left;
    min-width: 45px;
    min-height: 45px;
    text-align: center;
    margin: 5px 10px 5px 0;
    &:last-of-type { margin-right: 0 }
    a {
      display: block;
      padding: 10px 12px;
      border: 1px solid $border-color-01;
      &:hover { border-color: $border-color-02 }
    }
  }
}



/**
 * Pagination navbar
 */
.pagination {
  display: table;
  margin: 0 auto;
  list-style-type: none;
  li {
    float: left;
    margin: 0 3px;
    min-width: 45px;
    min-height: 45px;
    text-align: center;
    a {
      display: block;
      text-decoration: none;
      border: 1px solid $border-color-01;
      &:hover { border-color: $border-color-02 }
    }
    a, div {
      min-width: 41px;
      padding: 10px 12px;
      text-align: center;
      box-sizing: border-box;
    }
    div {
      border: 1px solid transparent;
      &.pager-edge {
        color: $border-color-01;
        border: 1px dashed;
      }
      &.current-page {
        font-weight: bold;
      }
    }
  }
}



/**
 * Task-lists
 */

.task-list {
  margin-left: 0;
  padding-left: $spacing-unit * 0.6;
}

.task-list-item {
  list-style-type: none;
  &-checkbox {
    position: relative;
    margin-right: $spacing-unit * 0.3;
    margin-left: -$spacing-unit * 0.5;
    appearance: none;
    border: 8px solid $border-color-01;
    vertical-align: text-top;
    z-index: -1;
    &::after {
      position: absolute;
      top: -8px;
      left: -3px;
      width: 4px;
      height: 10px;
      content: "";
      border: solid transparent;
      border-width: 0 2px 2px 0;
      transform: rotate(45deg);
    }
    &:checked::after { border-color: $border-color-03 }
  }
}

/**
 * Posts, tags and projects pages
 */

.tags-posts-title {
  text-align: center;
  padding-bottom: 1rem;
  border-bottom: 1px solid $border-color-01;
}

.list-item {
  margin-top: 0.2rem;
  
  .list-item-title {
    font-weight: bold;
  }

  .list-item-date {
    color: $brand-color-dimmed;
    font-style: italic;
  }
}


/**
 * Grid helpers
 */
@media screen and (min-width: $on-large) {
  .one-half {
    width: calc(50% - (#{$spacing-unit} / 2));
  }
}

/**
 * Figure numbering and description
 */

// Auto-number figures
.post-figure {
  counter-increment: figure-counter;
  margin: 30px 0;
  text-align: center;
  
  // GLightbox link inside figure
  a.glightbox {
    display: inline-block;
    cursor: zoom-in;
    
    &:hover {
      opacity: 0.9;
    }
  }
  
  figcaption {
    margin-top: 10px;
    padding: 0 20px;
    font-style: italic;
    color: var(--minima-text-color);
    font-size: 0.9rem;
    line-height: 1.4;
    
    &::before {
      content: "Figure " counter(figure-counter) ": ";
      font-weight: 600;
      font-style: normal;
      color: var(--minima-heading-color);
    }
  }
}
